<template>
  <div id="demo">
    <div></div>
    <ul class="userSelect" :style='"height:"+USHeight+"px"'>
      <li class="userOption" v-for = '(val,i) of list' @touchstart = 'moveStart($event,i)' @touchmove = 'doMove($event , i)' @touchend = 'moveEnd($event , i)' :style="'background:'+liStyle[i].bg+';box-shadow:'+liStyle[i].shadow+';top:'+liStyle[i].top+'px;'+'zIndex:'+liStyle[i].zIndex">
        <div>
          <p class="userOption-num" :style='"background:"+numColor[i]+";"'>{{liStyle[i].num}}</p>
          <span class="userOption-item">{{liStyle[i].lName}}</span>
        </div> 
        <div class="userOption-btn">
          <img :src="bgImg[i].imgUrl" alt="">
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Option from '../assets/img/option.png'
import Direction from '../assets/img/direction.png'
export default{
  data() {
    return {
      list:[
        {id:1},
        {id:2},
        {id:3},
        {id:4},
      ],
      leaved:false,
      canMove:false,
      numColor:[],
      liStyle:[],
      fixedTop:[],
      filterTop:[],
      bgImg:[],
      timer:'',
      y:0,
      USHeight:0,
      positionTop:0
    }
  },
  created() {
    this.addList();
  },
  mounted() {
    this.getUO();
  },
  methods: {
    moveStart(e,i){
      //console.log('开始');
      this.y = e.targetTouches[0].clientY;
      this.timer = setTimeout(()=>{
        if(!this.leaved && !this.canMove){
          this.liStyle[i].bg = '#07f';
          this.liStyle[i].shadow = '#000 0px 0px 20px 0px';
          this.bgImg[i].imgUrl = Direction;
          this.canMove = true;
          //console.log('长按');
        }
      },200);
    },
    doMove(e,i){
      //console.log('移动中');
      if(this.canMove){
        let moveY = e.targetTouches[0].clientY - this.y;
        let num = this.liStyle[i].num;
        let str = 0;
        this.liStyle[i].top = this.fixedTop[i] + moveY;
        this.liStyle[i].zIndex = 10;
        if(this.liStyle[i].top < this.filterTop[num-1]){
          if(this.liStyle[i].top < 0){
            this.liStyle[i].top = 0;
            return;
          }
          if(this.liStyle[i].top < this.filterTop[num - 2]){
            //console.log('向上');
            str = this.liStyle[i].num;
            this.liStyle[i].num = this.liStyle[num - 2].num;
            this.liStyle[num - 2].num = str;
            this.liStyle[num - 2].top = this.fixedTop[num - 1];
          }
        }else{
          //console.log('向下');
          let max = this.liStyle.length;
          if(this.liStyle[i].top > this.fixedTop[max - 1]){
            this.liStyle[i].top = this.fixedTop[max - 1];
            return;
          }
          str = this.liStyle[i].num;
          this.liStyle[i].num = this.liStyle[num].num;
          this.liStyle[num].num = str;
          this.liStyle[num].top = this.fixedTop[num - 1];
        }
      }
    },
    moveEnd(e,i){
      //console.log('结束');
      clearTimeout(this.timer);
      this.canMove = false;
      this.liStyle[i].bg = '#fff';
      this.liStyle[i].shadow = '';
      this.liStyle[i].zIndex = 0;
      this.liStyle[i].top = this.fixedTop[ this.liStyle[i].num-1 ];
      this.bgImg[i].imgUrl = Option;
      let arr = [];
      for (let j=0; j<this.list.length; j++){
        //let num = this.liStyle[j].num;
        arr[j] = this.liStyle[j];
        //this.liStyle[num - 1] = arr[j];
      }
      for (let j=0; j<this.list.length; j++){
        let num = arr[j].num;
        this.liStyle[num - 1] = arr[j];
      }
    },
    getUO(){
      let UO = document.getElementsByClassName('userOption');
      let getTop = 0;
      this.fixedTop = [];
      this.filterTop = [];
      this.positionTop = UO[0].offsetHeight;
      for (let i=0; i<UO.length; i++){
        this.liStyle[i].top = getTop;
        this.fixedTop.push(getTop);
        this.filterTop.push(getTop+UO[0].offsetHeight/2);
        getTop += UO[0].offsetHeight+1;
        this.USHeight = getTop;
      }
    },
    addList(){
      this.bgImg = [];
      this.numColor = [];
      this.liStyle = [];
      let names = ['选项A','选项B','选项C','选项D'];
      for(let i=0 ; i<this.list.length ; i++){
        this.bgImg.push({imgUrl : Option});
        this.numColor.push('#00f');
        this.liStyle.push({
          bg : '', 
          top : 0,
          num : i+1,
          shadow :'',
          lName : names[i],
          zIndex : 0
        });
      }
    },
  },
}
</script>

<style>
  ul,p{
    margin:0;
    padding:0;
  }
  ul{
    list-style:none;
  }
  .userSelect{
    position:relative;
    width:100%;
  }
  .userSelect .userOption{
    position:absolute;
    display:flex;
    justify-content:space-between;
    align-items:center;
    width:100%;
    box-sizing:border-box;
    padding:20px 0;
    border-top:1px solid #aaa;
  }
  .userOption div{
    display:flex;
  }
  .userOption .userOption-num{
    width:20px;
    height:20px;
    border-radius:50%;
    background:#aaa;
    color:#fff;
  }
  .userOption .userOption-item{
    margin-left:1rem;
    color:#000;
  }
  .userOption .userOption-btn{
    width:20px;
    height:20px;
  }
  .userOption .userOption-btn img{
    width:100%;
    height:100%;
    object-fit: fill;
  }
</style>


